a {
  color:blue;
}

.layout {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.post-body , .post-edit-body{
  margin: 10px 20px;
  width: 75%;
  min-width: 250px;
}

.post-nav {
  margin: 10px 20px;
  padding: 0 20px;
  width: 20%;
  border-left: 1px solid grey;
}

h1 {
  margin-left: 15px;
}

.post-nav > a {
  display: block;
  margin: 20px 0;
}

.post-row {
  margin: 20px 0 30px 0;
}

.post-row > a {
  margin: 5px;
}

.post-time {
  float: right;
}

.post-title {
  display: inline;
}

.edit-post-txt {
  width: 100%;
  height: 250px;
}

.post-edit-body > button {
  margin: 10px;
}

.post-edit-body > input {
  margin-bottom: 20px;
}

.create-new-title {
  width: 100%;
}

.create-new-text {
  width: 100%;
  height: 250px
}

